import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

const resultsContainer = document.querySelector('#results-container');

// Tek bir lightbox √∂rneƒüi, ba≈ülangƒ±√ßta null
let lightbox = null;

/**
 * Yeni g√∂rselleri mevcut listeye ekler, container'ƒ± temizlemez.
 * Lightbox'ƒ± refresh eder.
 */
export function renderImages(data) {
  // Her resim <li> i√ßinde olacak ≈üekilde markup olu≈üturuyoruz
  const markup = data.hits
    .map(
      hit => `
    
      <a class="image-card" href="${hit.largeImageURL}">
        <img src="${hit.webformatURL}" alt="${hit.tags}" />
        <div class="image-info">
          <p class="tags">Tags: ${hit.tags}</p>
          <div class="stats">
            <span>üëç ${hit.likes}</span>
            <span>üëÅÔ∏è ${hit.views}</span>
            <span>üí¨ ${hit.comments}</span>
            <span>‚¨áÔ∏è ${hit.downloads}</span>
          </div>
        </div>
      </a>
    
  `
    )
    .join('');

  // Yeni g√∂rselleri mevcut i√ßeriƒüe ekle
  resultsContainer.insertAdjacentHTML('beforeend', markup);

  // Eƒüer lightbox √∂rneƒüi yoksa olu≈ütur
  if (!lightbox) {
    lightbox = new SimpleLightbox('#results-container a', {
      captionsData: 'alt',
      captionDelay: 250,
      captionPosition: 'bottom',
    });
  } else {
    // Zaten varsa sadece refresh et
    lightbox.refresh();
  }
}
export function clearGallery() {
  const container = document.querySelector('#results-container');
  container.innerHTML = '';
}
export function showLoader() {
  const loader = document.getElementById('loader');
  loader.style.display = 'block';
}

export function hideLoader() {
  const loader = document.getElementById('loader');
  loader.style.display = 'none';
}
